const textSizes = ["15px", "15px", "16px"];
const headTextSizes = ["20px", "28px", "32px"];
const smallTextSizes = ["14px", "14px", "16px"];
const tagTextSizes = ["12px", "12px", "14px"];

const titleTextSizes = ["18px", "20px", "20px"];

function AuthorItems({ authors }) {
  return _.map(authors, (author) => (
    <Box
      sx={{
        ml: 3,
        py: "6px",
        px: "9px",
        color: "black",
        fontFamily: "Roboto Condensed, sans-serif",
        fontWeight: "light",
        borderStyle: "outset",
        borderColor: "white",
        borderWidth: "1px",
        bg: "#E8F6E0",
        // borderStyle: 'solid',
        // borderColor: 'plainDark',
        // borderWidth: "1px",
        fontSize: tagTextSizes,
        ":hover": {
          color: "greeDark",
        },
        display: "inline-block",
      }}
    >
      <a
        href={author.link}
        target="_blank"
        style={{ color: "inherit", textDecoration: "none" }}
      >
        with {author.name}
      </a>
    </Box>
  ));
}

function PaperTags({ tags }) {
  return _.map(tags, (tag) => (
    <Box
      sx={{
        ml: [2, 2, 3],
        py: "6px",
        px: "9px",
        color: "black",
        borderStyle: "outset",
        borderColor: "white",
        borderWidth: "1px",
        fontWeight: "light",
        fontFamily: "Roboto Condensed, sans-serif",
        bg: tag.color ? tag.color : "#plain",
        fontSize: tagTextSizes,
      }}
    >
      <a href={tag.link} style={{
        color: "inherit",
      }}>{tag.name}</a>
    </Box>
  ));
}

