class ResearchPage extends React.Component {
  constructor(props) {
    super();
    this.state = { myResearch: null };
  }
  componentDidMount() {
    if (typeof window !== "undefined") {
      this.setState({
        myResearch: [
          {
            title: "Do Urban Parks Promote Racial Diversity?",
            subtitle: "Evidence from New York City",
            id: "nyc-parks-diversity",
            year: 2020,
            authors: [],
            datavis: true,
            datavisLink: '/parks',
            abstract: 
            "While some prominent scholars and policymakers have argued that public space promotes racial diversity, empirically it remains unclear whether these places play a role in creating diverse social environments for the city's residents. Focusing on parks in New York City as the exemplar of modern public space, I estimate the causal relationship between access to parks and individually experienced diversity. To do so, I introduce a measure of racial diversity that captures one's level of exposure to diverse others in places visited on a daily basis, utilizing a novel dataset featuring individual GPS tracking data for more than 60 thousand Twitter users in the New York metro area. My empirical strategy relies on obtaining a time-varying measure of access to parks that incorporates information about ongoing construction and repair works across the city. The results show that additional 10 acres of parkland within the 5 km radius from home increase individual chances of encounters with other racial/ethnic groups by 1 p.p. The effect is sizable: for reference, transitioning from the current state to the random mixing scenario would require a 9 p.p increase in diversity for an average Black or African American individual and a 3.5 p.p increase for an average White. I also provide evidence to suggest that park accessibility affects the diversity of White and Black residents differently: for parks located closer to home, the effect appears to be more pronounced for Whites than Blacks, indicating that parks in the majority-white neighborhoods are able to attract a broader range of visitors compared to the local parks in majority-black neighborhoods.",
            pdflink: parksDiversityFile,
            carousel: <CarouselParks />,
            tags: [
              { name: "Job Market Paper", color: "oranLight", link: parksDiversityFile},
              { name: "Working Paper" ,link: parksDiversityFile},
              { name: "Download PDF" , link: parksDiversityFile},
            ],
            open: 1,
          },
          {
            title: "Do Local Businesses Benefit from Stadiums?",
            subtitle:
              "An Empirical Study of Major Sports Leagues Using Mobile Location Data",
            year: 2020,
            id: "stadiums-local-businesses",
            authors: [{ name: "Dmitry Sedov", link: "https://www.dsedov.io/" }],
            pdflink: stadiumsFile,
            tags: [{ name: "Working Paper",  link: stadiumsFile }, {name: "Download PDF",  link: stadiumsFile}],
            open: 0,
            abstract:
            "Stadiums cost hundreds of millions of dollars to build and are often subsidized by public sources. In many cases, subsidies are allocated on the premise that sports events benefit the local economy by bringing new customers to nearby businesses. We use daily foot traffic records covering major sports league facilities and their surrounding commercial establishments to pin down the size and spatial distribution of such spillovers. By employing fixed effects and IV estimation strategies, we show that benefits are heterogeneous across sports and business sectors. We find that baseball and football stadiums generate substantial traffic to businesses in the food & accommodation and retail trade categories. However, the corresponding spillover effects of basketball and hockey stadiums are not significant across the studied business sectors. Moreover, using the data on subsidies allocated to the facilities in our sample, we show that only the top quartile of the most attended baseball stadiums generate enough spillover benefits to offset the public funds they receive."
          },
        ],
      });
    }
  }
  render() {
    return (
      <Box>
        <span id="research"></span>
        <Box
          sx={{
            px: [0, 2, 2],
            pt: 2,
            mt: 3,
            // bg: 'red',
            pb: 2,
            borderTop: "2px solid",
            borderTopColor: "black",
            fontSize: ["10px", "12px", "15px"],
          }}
        >
          <h1>Research</h1>
          {/* <br/> */}
          <ResearchItems items={this.state.myResearch} />
        </Box>
      </Box>
    );
  }
}

export default ResearchPage;
